import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import _ from 'lodash';
import PropTypes from 'prop-types';

import {
  Wrapper,
  Container,
  Nav,
  PageLink,
  Spacer,
  TagLink,
} from './styles';

const tagQuery = graphql`
  query TagQuery {
    allMarkdownRemark {
      edges {
        node {
          frontmatter {
            tags
          }
        }
      }
    }
  }
`;

function getTagsFromQueryResult(data) {
  const posts = data.allMarkdownRemark.edges;
  return _.uniq(
    posts
      .filter(post => _.has(post, 'node.frontmatter.tags'))
      .reduce((tagList, post) => tagList.concat(post.node.frontmatter.tags), []),
  );
}

const pages = [
  { to: '/', text: 'Home' },
  { to: '/portfolio', text: 'Portfolio' },
  { to: '/resume', text: 'Resume' },
  { to: '/blog', text: 'Posts' },
];

const maxTags = 3;

const Tagbar = ({
  location,
}) => (
  <StaticQuery
    query={tagQuery}
    render={(data) => {
      // load all page data and extract distinct tags.
      const tags = getTagsFromQueryResult(data);
      return (
        <Wrapper>
          <Container>
            {pages.map(page => (
              <Nav>
                <PageLink to={page.to} active={location.pathname === page.to}>
                  { page.text }
                </PageLink>
              </Nav>
            ))}
            <Spacer />
            {tags
              .slice(0, maxTags)
              .map(tag => (
                <Nav>
                  <TagLink to={`/tags/${_.kebabCase(tag)}`}>
                    { tag }
                  </TagLink>
                </Nav>
              ))}
            <Nav>
              <TagLink to="/tags/">More</TagLink>
            </Nav>
          </Container>
        </Wrapper>
      );
    }}
  />
);

Tagbar.propTypes = {
  location: PropTypes.string.isRequired,
};

export default Tagbar;
